import matter from 'gray-matter'

// Import markdown files directly - this works in both dev and production
// For each new post, add an import line here
import firstPostContent from '/posts/first-post.md?raw'

// Define all posts with their imports
const postImports = [
  {
    slug: 'first-post',
    content: firstPostContent
  }
  // Add new posts here like this:
  // { slug: 'second-post', content: secondPostContent }
]

// Parse all markdown files and extract frontmatter + content
const parsePosts = () => {
  const posts = []
  
  for (const postImport of postImports) {
    const { data, content } = matter(postImport.content)
    const readTime = calculateReadTime(content)
    
    posts.push({
      slug: postImport.slug,
      title: data.title,
      date: data.date,
      excerpt: data.excerpt,
      content,
      tags: data.tags || [],
      readTime,
      image: data.image || null
    })
  }
  
  return posts.sort((a, b) => new Date(b.date) - new Date(a.date))
}

// Get all posts
export const posts = parsePosts()

export const getPostBySlug = (slug) => {
  return posts.find(post => post.slug === slug)
}

export const getAllPosts = () => {
  return posts
}

export const getPostsByTag = (tag) => {
  return posts.filter(post => post.tags.includes(tag))
}

// Function to calculate read time (rough estimate)
export const calculateReadTime = (content) => {
  const wordsPerMinute = 200
  const wordCount = content.split(/\s+/).length
  return Math.ceil(wordCount / wordsPerMinute)
} 